import React, { Suspense, useRef, useEffect, useState, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { shaderMaterial, Plane } from '@react-three/drei';
import * as THREE from 'three';
import { extend } from '@react-three/fiber';
import { vertexShader, fragmentShader } from './components/shaders';
import { Buffer1Material, Buffer2Material } from './components/buffers';

// Define the VideoShaderMaterial using the shaderMaterial utility
const VideoShaderMaterial = shaderMaterial(
  {
    iResolution: new THREE.Vector2(window.innerWidth, window.innerHeight),
    iChannel0: null, // First texture (video texture)
    amplitude: 1, // Dynamic amplitude
    time: 5, // Animation time
    VERT: false, // Direction for displacement, false for horizontal
  },
  vertexShader,
  fragmentShader
);

// Extend the material
extend({ VideoShaderMaterial });

// VideoMaterial component to load and play the video
function VideoMaterial({ src, amplitude, time }) {
  const shaderRef = useRef();
  const videoRef = useRef(document.createElement('video'));
  
  useEffect(() => {
    const video = videoRef.current;
    video.src = src;
    video.crossOrigin = 'anonymous';
    video.loop = true;
    video.muted = true;
    video.play();

    // Create a VideoTexture and assign it to the shader
    const texture = new THREE.VideoTexture(video);
    console.log(texture, '<< texture')
    shaderRef.current.iChannel0 = texture; // Assign video texture to iChannel0
  }, [src]);

  useFrame(({ clock }) => {
    shaderRef.current.time = clock.getElapsedTime(); // Update time
    shaderRef.current.amplitude = amplitude; // Pass dynamic amplitude to shader
  });

  return <videoShaderMaterial ref={shaderRef} />;
}

export default function App() {
  const [amplitude, setAmplitude] = useState(1.5);
  const [time, setTime] = useState(5);
  const [vid, setVid] = useState(0);
  const vids = ['drum1.mp4', 'mov.mp4'];

  const buffer1 = useMemo(() => new THREE.WebGLRenderTarget(window.innerWidth, window.innerHeight), []);
  const buffer2 = useMemo(() => new THREE.WebGLRenderTarget(window.innerWidth, window.innerHeight), []);

  // Increase amplitude with an example setInterval
  useEffect(() => {
    const interval = setInterval(() => {
      // TODO this is bad apparently, do useframe for state mutation
      // seems like usrframe avoids rerenders of state modifications
      setAmplitude(amplitude + 0.01);
      setTime(time + 0.5);
      // setVid(vid === 0 ? 1 : 0);
      // console.log(vid)
    }, 2000);

    return () => clearInterval(interval);
  }, [time]);

  return (
    <Canvas camera={{ position: [0, 0, 3], zoom: 1 }}>
      <Plane args={[4, 2.25]} position-y={0}>
        <VideoMaterial src={vids[vid]} amplitude={amplitude} time={time} />
      </Plane>
    </Canvas>
  );
}